* {
  margin: 0px;
  padding: 0px;
}

li {
  list-style-type: none;
}

header {
  width: 100%;
  height: 48px;
  display: flex;
  align-items: center;
  box-shadow: 0px 3px 5px -1px rgba(136, 135, 135, 0.77);
}

.fa-bars {
  margin: auto 2%;
  font-size: 23px;
  font-weight: 600;
  color: #888787;
}

.fa-ellipsis-v {
  position: absolute;
  right: 2%;
  font-size: 23px;
  font-weight: 600;
  color: rgb(136, 135, 135);
}

main {
  width: 100%;
  display: flex;
  justify-content: space-around;
}

.tool {
  width: 17%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.palette, .colors-part {
  width: 70%;
  height: 200px;
  margin-top: 25px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: flex-start;
  border: 1px solid #d4d4d4;
  box-shadow: 0px 5px 7px -1px rgba(136, 135, 135, 0.77);
  color: #7e7c7c;
}

.palette p {
  width: 90%;
  display: flex;
  margin-left: 15px;
  font-size: 15px;
}

.palette p span, .colors-part ul li p {
  margin-left: 15px;
}

.palette p i {
  margin-top: 3px;
}

.palette p:hover, .colors-part li:hover {
  cursor: pointer;
  color: #ad4646;
  font-weight: 800;
}

.line {
  width: 100%;
  height: 2px;
  background-color: #d4d4d4;
}

.colors-part ul li div {
  width: 20px;
  height: 20px;
  border: 1px solid #252424;
  border-radius: 100%;
}

.colors-part ul {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: flex-start;
}

.colors-part ul li {
  width: 90%;
  display: flex;
  margin-left: 15px;
  font-size: 15px;
}

.frame_border {
  width: 135px;
  height: 130px;
  margin: 10px 0px;
  border: 3px solid #ffd700;
}

.frame_border-none {
  width: 135px;
  height: 130px;
  margin: 10px 0px;
  border: 3px solid #e2e0e0;
}

.red {
  background-color: #ff0000;
}

.blue {
  background-color: #0000ff;
}

.green {
  background-color: #168b47;
}

.grey {
  background-color: #b9b4b4;
}

.grey-light {
  background-color: #d8d8d8;
}

.canvas_place .frame {
  width: 460px;
  height: 460px;
  margin-top: 25px;
}

.canvas_place .frame-area {
  width: 460px;
  height: 460px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between;
  align-items: stretch;
}

.canvas_place .frame-area div {
  width: 460px;
  height: 150px;
  display: flex;
  justify-content: space-between;
}

.canvas_place .frame-area div div {
  width: 150px;
  height: 150px;
}

.transform {
  border: 1px solid rgba(128, 128, 128, 0);
  border-radius: 100%;
}
