.frame {
  width: 130px;
  margin-bottom: 8px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  border: 2px solid #d4d4d4;
  box-shadow: 0px 5px 7px -1px rgba(136, 135, 135, 0.77);
  color: #7e7c7c;
  margin-top: -59px;
}

.frame-area {
  width: 120px;
  height: 120px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  border: 1px solid #808080;
  align-items: stretch;
  font-size: 15px;
}

.frame-area div {
  width: 120px;
  height: 38px;
  display: flex;
  justify-content: space-between;
}

.frame-area div div {
  width: 38px;
  height: 38px;
}
